import React from "react"
import { Link } from "gatsby"
import CookieConsent, { getCookieConsentValue } from "react-cookie-consent";
import "@fontsource/noto-sans-jp/400.css";
import "@fontsource/noto-sans-jp/500.css";
import Header from "./header"
import Footer from "./footer"
import ScrollTop from "./scroll-top"
import GaDisable from "./ga-disable";

export default function Layout({ children }) {
  //console.log("CookieConsentValue=[" + getCookieConsentValue() + "]");

  let gaDisabled = false
  if( getCookieConsentValue() === "false" ) {
    gaDisabled = true
    console.log("gaDisabled=[" + gaDisabled + "]")
  }
   
  return (
    <>
      <CookieConsent
        location="bottom"
        buttonText="Cookie"
        style={{ background: "#2B373B", fontSize: "12px" }}
        buttonStyle={{ color: "#4e503b", fontSize: "13px" }}
        enableDeclineButton
        declineButtonText="Cookie"
        declineButtonStyle={{ color: "#ffffff", fontSize: "13px" }}
        expires={150}
        onAccept={() => {
          //console.log("CookieConsentValue=[" + getCookieConsentValue() + "]");
        }}
        onDecline={() => {
          //console.log("CookieConsentValue=[" + getCookieConsentValue() + "]");
        }}
      >
        Cookie{" "}
        <span style={{ fontSize: "10px" }}>
          <Link to={`/privacy-policy`}></Link>
        </span>
      </CookieConsent>
      <GaDisable disabled={gaDisabled} />
      <Header/>
      <main>{children}</main>
      <Footer/>
      <ScrollTop/>
    </>     
  )
}
