import React from "react"
import { css } from "@emotion/react"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faAngleUp } from "@fortawesome/free-solid-svg-icons"
import scrollTo from 'gatsby-plugin-smoothscroll';

export default function ScrollTop() {

  return (
    <div
			role="none"
      css={css`
        background-color: #ff6113;
	      border-radius: 50%;
	      color: #fff;
	      font-size: 14px;
	      height: 50px;
	      line-height: 50px;
	      outline: 0;
	      position: fixed;
	      bottom: 10px;
	      right: 10px;
	      text-align: center;
	      width: 50px;
	      z-index: 1;
	      opacity: 0.8;
      `}
			onClick={() => scrollTo('html')}
			onKeyDown={() => scrollTo('html')}
    >
      <FontAwesomeIcon 
        icon={faAngleUp} 
        css={css`
        	font-weight: 900;
        	font-size: 25px;
        	color: #fff;
        	position: absolute;
        	width: 25px;
        	height: 25px;
        	top: 0;
        	bottom: 0;
        	right: 0;
        	left: 0;
        	margin: auto;
        	text-align: center;
        `}
      />
    </div>
  )
}