import React from "react"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import { useStaticQuery, graphql } from "gatsby"
import { css } from "@emotion/react"
import ContactBtn from "./contact-btn"
import "../styles/header.css"

export default function Header() {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
          }
        }
      }
    `
  )

  return (
    <header className="header">
      <div className="headerNavOuter">
        <h1 className="siteIcon">
          <Link to={`/`}>
            <StaticImage
              src="../images/header_logo.png"
              alt={site.siteMetadata.title}
              placeholder="blurred"
              css={css`
                width: 303.25px;
                margin: 0px 110px 0px 0;
                @media screen and (min-width:768px) and (max-width:1024px) {
                  width: 100%;
                }
                @media(max-width: 769px) {
                  width: 100%;
                }
              `}
            />
          </Link>
        </h1>
    
        <nav className="headerNav">
          <ul className="navList">
            <ContactBtn/>
            <li className="navItem">
              <Link to={`/`} className="hoverFloatBtn"></Link>
            </li>
            <li className="navItem">
              <Link to={`/case-study/articles/`} className="hoverFloatBtn"></Link>
            </li>
            <li className="navItem">
              <Link to={`/dx-council`} className="hoverFloatBtn">DX</Link>
            </li>
            <li className="navItem">
              <Link to={`/column`} className="hoverFloatBtn"></Link> 
            </li>
            <li className="navItem">
              <Link to={`/about-us`} className="hoverFloatBtn"></Link>
            </li>
          </ul>
        </nav>
      </div>
    </header>
  )
}