import React from "react"
import { Helmet } from "react-helmet"

export default function GaDisable({ disabled }) {
  return (
    <Helmet>   
        {disabled &&
          <script>
          window['ga-disable-G-M6YX1138KC'] = true;
          window['ga-disable-UA-196140002-1'] = true;
          </script>
        }
    </Helmet>
  )
}