import React from "react"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import { useStaticQuery, graphql } from "gatsby"
import { css } from "@emotion/react"
import "../styles/footer.css"

export default function Footer() {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
          }
        }
      }
    `
  )

  return (
    <footer>
      <div className="footer_inner clearfix">
        <div className="flex">
          <div className="flex-item left">
            <Link to={`/`}>
              <StaticImage
                src="../images/footer-logo.png"
                alt={site.siteMetadata.title}
                placeholder="blurred"
                css={css`
                  width: 307px;
                  margin-bottom: 42px;
                  @media(max-width: 769px) {
                    width: 60%;
                    margin: 0 0px 20px;
                  }
                `}
              />
            </Link>
            <div className="flex flex-child">
              <div className="fles-item left">
                <p><br className="pc"/>IT<br className="pc"/><br className="pc"/></p>
              </div>
              <div className="flex-item right">
                <div className="footer-btn">
                  <Link to={`/contact-us`}>
                    <br/>
                  </Link>
                </div>
              </div>
            </div>
          </div>
          <div className="flex-item right">
            <a href="https://line.me/R/ti/p/%40639zimnh" target="_blank" rel="noopener noreferrer">
              <div className="footer_line">
                <p><br/><br/></p>
              </div>
            </a>
          </div>
        </div>
        <div className="footer-nav_inner flex">
          <ul className="global pc">
            <li className="nav_menu_li">
              <Link to={`/`} className="hoverFloatBtn"></Link>                  
            </li>
            <li className="nav_menu_li">
              <Link to={`/case-study/articles/`} className="hoverFloatBtn"></Link>
            </li>
            <li className="nav_menu_li">
              <Link to={`/dx-council`} className="hoverFloatBtn">DX</Link>
            </li>
            <li className="nav_menu_li">
              <Link to={`/column`} className="hoverFloatBtn"></Link>
            </li>
            <li className="nav_menu_li">
              <Link to={`/about-us`} className="hoverFloatBtn"></Link>
            </li>
            <li className="nav_menu_li">
              <Link to={`/contact-us`} className="hoverFloatBtn"></Link>
            </li>
          </ul>
          <ul className="snsglobal">
            <li>
              <a href="https://www.facebook.com/HotsoulDotJp/" target="_blank" rel="noopener noreferrer">
                <StaticImage
                  src="../images/icon-fb.png"
                  alt="facebook"
                  placeholder="blurred"
                />
              </a>
            </li>
            <li>
              <a href="https://line.me/R/ti/p/%40639zimnh" target="_blank" rel="noopener noreferrer">
                <StaticImage
                  src="../images/icon-line.png"
                  alt="LINE"
                  placeholder="blurred"
                />
              </a>
            </li>
          </ul>
        </div>
      </div>
      <div className="copy">
        <div className="copy-inner flex">
          <ul className="clearfix">
            <li className="flex-item item1">
              <p><Link to={`/privacy-policy`}></Link></p>
            </li>
          </ul>
          <div className="flex-item item3">
            <p> {new Date().getFullYear()} HotSoul Inc.</p>
          </div>
        </div>
      </div>
    </footer>
  )
}